ado tonía, es la sensación perceptiva relacionada con la frecuencia de vibración de las cuerdas vocales. El tono puede ser grave o agudo, y está determinado por las características físicas de las cuerdas vocales (longitud, tensión y grosor). Una modulación natural del tono genera credibilidad, mientras que un tono fingido puede resultar poco convincente y afectar la autenticidad del mensaje.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"Entonación"}},[_c('div',{staticClass:"col-md-11 mb-4 mb-md-0"},[_c('p',{staticClass:"px-3"},[_vm._v("Es la variación rítmica del tono de voz a lo largo del discurso. A través de la entonación se puede enfatizar palabras o frases clave, evitar la monotonía y mantener la atención del oyente. Incluir pausas adecuadas, cambios de ritmo y volumen contribuye a una comunicación más efectiva y dinámica.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"Timbre"}},[_c('div',{staticClass:"col-md-11 mb-4 mb-md-0"},[_c('p',{staticClass:"px-3"},[_vm._v("Es la cualidad única de cada voz que la hace reconocible. Permite distinguir dos voces con el mismo tono e intensidad. El timbre es personal y está relacionado con la configuración fisiológica de cada individuo; puede ser melodioso, suave, áspero o tímido, y es parte esencial de la identidad vocal de cada ser humano.")])])])])],1)]),_c('div',{staticClass:"tarjeta bgbanner p-3 mb-3"},[_c('div',{staticClass:"row justify-content-around align-items-center"},[_vm._m(13),_c('div',{staticClass:"col"},[_c('div',{staticClass:"row justify-content-between align-items-center"},[_vm._m(14),_c('div',{staticClass:"col-sm-auto p-4"},[_c('a',{staticClass:"boton color-acento-botones",attrs:{"href":('https://www.youtube.com/watch?v=jDTU8FAkWV0'),"target":"_blank"}},[_c('span',[_vm._v("Ver video ")]),_c('i',{staticClass:"fas fa-link"})])])])])])]),_vm._m(15),_c('p',[_vm._v("Además del dominio técnico de la voz, es indispensable preparar con anticipación el contenido del mensaje. El conocimiento profundo del tema, el uso de ayudas audiovisuales y el entrenamiento previo permiten proyectar seguridad y claridad al momento de hablar. Se recomienda:")]),_vm._m(16),_vm._m(17),_c('p',{staticClass:"mb-5"},[_vm._v("En el marco de la formación profesional impartida por el SENA, los aprendices tienen la oportunidad de desarrollar y fortalecer sus habilidades comunicativas, incluyendo la oratoria. Esto implica mucho más que saber hablar: la oratoria es el arte de expresarse de forma clara, estructurada y persuasiva ante un público, como lo expresa conceptos (2020):")]),_vm._m(18),_c('p',[_vm._v("Así, desde una mirada autocrítica, cada aprendiz puede descubrir y potenciar al orador que hay en sí mismo, desarrollando competencias esenciales no solo para la vida académica y laboral, sino también para la interacción social y el liderazgo en diversos contextos.")]),_c('separador'),_vm._m(19),_c('p',[_vm._v("Por otra parte, la oratoria no se puede simplificar al proceso del habla, toda vez que hablar implica escuchar activamente, lo que a su vez requiere activar todos los sentidos en este ejercicio y así lograr leer la emoción del otro, cualquiera que este sea (público, grupo pequeño o persona), conectarse con sus necesidades y lógicas, aceptar la diferencia, conversar y crecer con ella, en fin, llegar a ser empático con las demás personas en los diferentes contextos en que se interactúa.")]),_vm._m(20),_c('div',{staticClass:"row justify-content-center mb-5",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-6 mb-3"},[_c('LineaTiempoD',{staticClass:"color-acento-botones activo"},[_c('div',{staticClass:"row",attrs:{"numero":"1","titulo":"Prestar atención"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',{staticClass:"text-small"},[_vm._v("Concéntrate en lo que te están diciendo.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"2","titulo":"Evita juzgar"}},[_c('div',{staticClass:"