o aquello que se desconoce o, por el contrario, si se domina un tema profundamente, es necesario saber simplificarlo y expresarlo de forma comprensible para el receptor.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"4","titulo":"El sistema sociocultural"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',{staticClass:"text-small"},[_vm._v("Las diferencias sociales y culturales condicionan el estilo y la forma en que las personas se comunican. Las interpretaciones y modos de expresión pueden variar considerablemente según el entorno cultural, lo que exige sensibilidad y adaptabilidad por parte del comunicador.")]),_c('p',{staticClass:"text-small"},[_vm._v("Por su parte, Stoner (1996, pp. 376 - 410) añade que la diferencia entre una comunicación efectiva e inefectiva puede estar en la manera en que las partes involucradas gestionan las diferencias de percepción, ya que las personas interpretan los mensajes según sus conocimientos, experiencias y contexto. Además, las emociones como la ira, el miedo, el amor o el odio también afectan la forma en que se recibe y procesa un mensaje, influyendo directamente en la comprensión y en la calidad del intercambio comunicativo.")])])])])],1),_vm._m(27)])],1)],1)
