 tipografías, e incluso los colores o formatos de mensajes en redes como Twitter, WhatsApp o correos electrónicos. Estos elementos, aunque digitales, también comunican actitudes, emociones o intenciones, y permiten que la comunicación no verbal continúe desempeñando un papel fundamental en los entornos virtuales.")])])])
