},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center align-items-center mb-5",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-12"},[_c('p',[_vm._v("El discurso oral constituye una herramienta fundamental para la transmisión de ideas, conocimientos y emociones en diversos contextos sociales, académicos y profesionales. A través de la palabra hablada, las personas logran establecer conexiones significativas, influir en los demás, generar aprendizajes y resolver conflictos. En este sentido, desarrollar habilidades de expresión oral no solo mejora la comunicación interpersonal, sino que también fortalece la seguridad, el liderazgo y la capacidad argumentativa del hablante.")]),_c('p',[_vm._v("Dentro del ámbito comunicativo, la charla y la exposición se destacan como dos formas estructuradas de discurso oral. Ambas tienen como propósito principal informar, explicar o desarrollar un tema ante un público determinado. Sin embargo, presentan diferencias en su estilo y nivel de formalidad. La charla suele ser más informal y cercana, con un tono conversacional que facilita la participación del oyente; en cambio, la exposición requiere una preparación más rigurosa y sigue una estructura lógica que incluye introducción, desarrollo y conclusiones.")]),_c('p',[_vm._v("Tanto en la charla como en la exposición, es indispensable cuidar elementos como la claridad del mensaje, la organización del contenido, el uso adecuado del lenguaje verbal y no verbal, y la adaptación al contexto y al público. Además, el dominio de técnicas oratorias como la modulación de la voz, la entonación, el manejo del ritmo y las pausas, así como el contacto visual y el uso de apoyos visuales, contribuyen a una presentación efectiva y memorable.")])])])
